import React, { Component } from 'react'
import {Card, CardColumns, Badge, Pagination} from 'react-bootstrap'


import devgrub from '../assets/images/devgrub.png';
import youtube from '../assets/images/youtube.png';
import evverest from '../assets/images/evverest.png';

export default class ProjectCard extends Component {

    constructor(props) {
        super(props);
        this.state = {
            items: [
                {
                    id: 0,
                    title: 'Card title',
                    text: 'This card has supporting text below as a natural lead-in to additional \n content.',
                    imgSrc: devgrub,
                    link: 'https://devgrub.com',
                    feature: ['first', 'second', 'third'],
                    dataAdded: 'Added on 12 Feb 2020'
                },
                {
                    id: 1,
                    title: 'Garrett Love',
                    subTitle: 'YouTube channel',
                    imgSrc: youtube,
                    link: 'https://www.youtube.com/channel/UCxSITxL2JbF229OGCqieVZw',                    
                    feature: ['first', 'second', 'third'],
                    dataAdded: 'Added on 12 Feb 2020'
                },
                {
                    id: 2,
                    title: 'Evverest',
                    subTitle: 'A social network for developers',
                    imgSrc: evverest,
                    link: 'https://github.com/garrettlove8/evverest',                    
                    feature: ['first', 'second', 'third'],
                    dataAdded: 'Added on 12 Feb 2020'
                },
                {
                    id: 3,
                    title: 'Garrett Love',
                    subTitle: 'YouTube channel',
                    imgSrc: devgrub,
                    link: 'https://www.youtube.com/channel/UCxSITxL2JbF229OGCqieVZw',                    
                    feature: ['first', 'second', 'third'],
                    dataAdded: 'Added on 12 Feb 2020'
                },
                {
                    id: 4,
                    title: 'Evverest',
                    subTitle: 'A social network for developers',
                    imgSrc: evverest,
                    link: 'https://github.com/garrettlove8/evverest',                    
                    feature: ['first', 'second', 'third'],
                    dataAdded: 'Added on 12 Feb 2020'
                },
                {
                    id: 5,
                    title: 'Garrett Love',
                    subTitle: 'YouTube channel',
                    imgSrc: devgrub,
                    link: 'https://www.youtube.com/channel/UCxSITxL2JbF229OGCqieVZw',                    
                    feature: ['first', 'second', 'third'],
                    dataAdded: 'Added on 12 Feb 2020'
                },
                {
                    id: 6,
                    title: 'Evverest',
                    subTitle: 'A social network for developers',
                    imgSrc: evverest,
                    link: 'https://github.com/garrettlove8/evverest',
                    feature: ['first', 'second', 'third'],
                    dataAdded: 'Added on 12 Feb 2020'
                },
            ],
            pageNumber: 1,
        }
    }

    pageChanged = (e) => {
        const pageNumber = parseInt(e.target.text)
        this.setState({pageNumber});
    }

    render() {
        let numItemsPerPage = 6 
        let active = this.state.pageNumber;
        let items = [];
        let totalPages = Math.ceil(this.state.items.length/numItemsPerPage);
        for (let number = 1; number <= totalPages; number++) {
            items.push(
                <Pagination.Item key={number} active={number === active}>
                    {number}
                </Pagination.Item>,
            );
        }
        console.log(numItemsPerPage*(active-1),(numItemsPerPage*active)+1)
        const resultItems = this.state.items.slice(numItemsPerPage*(active-1),(numItemsPerPage*active))
        console.log(resultItems)
        return (
            <>
            
                <CardColumns>

                    {resultItems.map((itm) => (
                        <Card  key={itm.id}>
                            <Card.Img variant="top" src={itm.imgSrc} style={{ height:'75vh' }} />
                            <Card.Body>
                                <Card.Title>{itm.title}</Card.Title>
                                <Card.Text>
                                    {itm.subTitle}{itm.id}
                                </Card.Text>
                                {itm.feature.map((fea) => (
                                <Badge variant="secondary" className="m-2">{fea}</Badge>
                                ))}
                            </Card.Body>
                            <Card.Footer>
                                <small className="text-muted">{itm.dataAdded}</small>
                            </Card.Footer>
                        </Card>
                    ))}

                </CardColumns>
                <Pagination size="lg" onClick={this.pageChanged}>{items}</Pagination>
            </>
        )
    }
}
